/* General Styles */
.section{
  padding-top: 4.8rem;
  padding-bottom: 3.2rem;
  text-align: center;
  line-height: 1.5em;
}
.section img{
  max-width: 23%;
  margin-bottom: 3.2rem;
}
  h1{
    font-family: "Hanken book";
    font-size: 4.3rem;
    text-align: center;
    /* letter-spacing: 1.5px; */
    margin-bottom: 3rem;
    font-weight: 400;
    line-height: 1;
  }
  h2{
    font-family: "Hanken book";
    font-size: 3.6rem;
    text-transform: uppercase;
    line-height: 1.1em;
    font-weight: 400;
    color: #2d2c4c;
  }
  h3{
    font-family: "Hanken book";
    font-size: 2.5rem;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
    line-height: 1;
  }
  p{
    color: #2d2c4c;
    font-size: 1.6rem;
    line-height: 1.5;
    margin-bottom: 3.2rem;
  }
  button{
    font-family: "Hanken book";
    display: inline-block;
    border-radius: 4px;
    background-color: #3898EC;
    color: white;
    border: 0;
    cursor: pointer;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
  ul{
      list-style: none;
  }
.label{
    font-size: 1.5rem;
    line-height: 1.8em;
    background-color: #2d2c4c;
    color: #fff;
    margin-bottom: 3.2rem;
    padding: 0 0.8rem;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
  .title{
    font-family: "Hanken book";
    font-size: 1.6rem;
    font-weight: 100;
    color: #20a5e6;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    padding-bottom: 0.5rem;
  }
  .container{
    padding: 0 1.6rem;
  }
  /* Hero Section*/
  .section-hero{
    background-color: #fcd750;
    text-align: center;
    padding-top: 8rem;
    padding-bottom: 3.2rem;
  }
  .hero-message{
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #2d2c4c;
  }
  .smile{
    margin: 0 3rem;
  }
  .smile img{
    max-width: 100%;
    width: 100%;
    margin-bottom: 3.2rem;
  }
  .subtitle{ /*section hero*/
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 1.5em;
    text-align: center;
    /* padding: 0 3rem; */
    color: #2d2c4c;
    margin-bottom: 4.8rem;
  }
  .items{
    /* padding: 0 2rem; */
  }
  .item{
    margin-bottom: 2.5rem;
  }
  .item img{
    width: 60px;
    height: 66px;
    margin-bottom: 2rem
  }
  .item button{
    font-family: "Hanken book";
    border-radius: 3px;
    font-size: 1rem;
    background-color: #20a5e6;
    padding: 1rem 1.2rem ;
  }
  /* Process Section */
  .process-section{
    background-color: white;
    color: #2d2c4c;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .content{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .section-header{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3.2rem;
  }
  /* Features Section */
  .section-features{
    background-color: #bce3f5;
    color: #2d2c4c;
  }
  .feature-item{
    padding: 0 1.6rem;
    margin-bottom: 3.2rem;
  }
  .feature-item img{
    max-width: 100%;
    margin-bottom: 3.2rem;
  }
  /* Services Section */
  .section-services{
    background-color: #f2f2f2;
  }
  /* About Section */
  .section-about{
    background-color: #fcd750;
    color: #2d2c4c;
    padding: 3.2rem 0;
    text-align: center;
  }
  .section-about .container{
    margin: 0 3.2rem;
  }
  .section-about button{
    font-size: 1.4rem;
    background-color: #2d2c4c;
    padding: 2rem 2.4rem 1.6rem;
  }
  /* How Section */
  .section-how{
    background-color: #f2f2f2;
  }
  .section-how .subtitle{
    margin-bottom: 0;
  }
  .section-how button{
    font-size: 1.4rem;
    background-color: #20a5e6;
    padding: 2rem 2.4rem 1.6rem;
  }